const app = getApp();
import pageBottom from '@/components/page-bottom';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
export default {
    components: {
        pageBottom,uniLoadMore
    },
    data() {
        return {
            cateMaskState: 0, //分类面板展开状态
            headerPosition: "fixed",
            headerTop: "0px",
			showSaleBox:0,  //是否开启设置铺货
            loadingType: "more", //加载更多状态
            filterIndex: 0,
			selectShopType:0, //0=未铺货商品 1=已铺货商品
            cateId: 0, //已选三级分类id
            priceOrder: 0, //1 价格从低到高 2价格从高到低
            cateList: [],
            storeList: [],
            goodsList: [],
            page: 1,
			priceInfo:{
				goods_id:0,
				cost_price:0,
				add_price:0,
				sale_price : 0,
				profits : 0,
			}
        };
    },

    onLoad(options) {





        this.cateId = options.tid;
        this.loadCateList(options.fid, options.sid);
    },
    onShow() {
        if (this.play_media == 1) {
            this.play_media = 0;
        } else {
            this.loadData("refresh");
        }
    },
    onPageScroll(e) {
        //兼容iOS端下拉时顶部漂移
        if (e.scrollTop >= 0) {
            this.headerPosition = "fixed";
        } else {
            this.headerPosition = "absolute";
        }
    },
    //下拉刷新
    onPullDownRefresh() {
        this.loadData("refresh");
    },
    //加载更多
    onReachBottom() {
        this.loadData();
    },
    methods: {
		//加载分类
		async loadCateList(fid, sid) {
		    let list = await this.$api.json("cateList");
		    let cateList = list.filter(item => item.pid == fid);
		
		    cateList.forEach(item => {
		        let tempList = list.filter(val => val.pid == item.id);
		        item.child = tempList;
		    });
		    this.cateList = cateList;
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = "add", loading) {
		    //没有更多直接返回
		    if (type === "add") {
		        if (this.loadingType === "nomore") {
		            return;
		        }
		        this.loadingType = "loading";
		    } else {
		        this.loadingType = "more";
		    }
		
		    if (type === "refresh") {
		        this.page = 1;
		    }
		
		    uni.request({
		        url: this.$Url + "/clmsj/goods/getall_goodslist",
		        header: {
		            auth: uni.getStorageSync("session_key")
		        },
		        data: {
		            selectShopType: this.selectShopType,
		            page: this.page
		        },
		        method: "POST",
		        success: res => {
					uni.hideLoading();
		            if (res.data.code == 1) {
		                uni.navigateTo({
		                	url: "/pages/user/user"
		                });
		                return;
		            }
		
		            if (res.data.code == 0) {
		                let goodsList = res.data.data;
		                this.page++;
		                if (type == "refresh") {
		                    this.goodsList = goodsList;
		                } else {
		                    this.goodsList = this.goodsList.concat(goodsList);
		                }
		
		                //判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
		                this.loadingType =
		                    goodsList.length < 10 ? "nomore" : "more";
		                if (type === "refresh") {
		                    if (loading == 1) {
		                        uni.hideLoading();
		                    } else {
		                        uni.stopPullDownRefresh();
		                    }
		                }
		            }
		        }
		    });
		},
		setAddPrice_txt(e){
			this.priceInfo.add_price = e.detail.value;
			this.setAddPrice(this.priceInfo.add_price);
		},
		setAddPrice(price){
			console.log(this.priceInfo);
			
			console.log(this.priceInfo.cost_price);
			console.log(price);
			console.log(Number(this.priceInfo.cost_price + price).toFixed(2));
			
			this.priceInfo.add_price = price;
			this.priceInfo.sale_price = (Number(this.priceInfo.cost_price) + Number(price)).toFixed(2);
			this.priceInfo.profits = (Number(price) * 0.99).toFixed(2);
			this.$forceUpdate();
		},
		hideSetSale(){
			this.showSaleBox = 0;
		},
		showSetSale(item,index){
			if(item.status==1){
				this.priceInfo = {
					index:index,
					status:item.status,
					goods_id: item.goods_id,
					cost_price:item.shop_price,
					proposal_price:item.proposal_price,
					add_price: 0,
					sale_price : 0,
					profits : 0,
				}
				this.pickBtn();
			}else{
				if(Number(item.shop_price) <= 0){
					this.$api.msg("该商品暂不支持铺货销售！");
					return;
				}
				this.priceInfo = {
					index:index,
					status:item.status,
					goods_id: item.goods_id,
					cost_price:item.shop_price,
					proposal_price:item.proposal_price,
					add_price: 0,
					sale_price : 0,
					profits : 0,
				}
				this.showSaleBox = 1;
			}
		},
		//铺货
		async pickBtn(){
			if(this.priceInfo.status == 0){
				if(this.priceInfo.add_price <= 0 ){
					this.$api.msg("请设置加价价格！");
					return;
				}
			}
			uni.showLoading({
				title: "铺货中..."
			});			
			const res = await app.request(this.$Url+'/clmsj/goods/getgoods', {
				goods_id: this.priceInfo.goods_id,
				raise_price : this.priceInfo.add_price
			});
			this.goodsList[this.priceInfo.index].select_shop = res.data.data.select_shop;
			uni.hideLoading();
			this.$api.msg('铺货成功！');
			this.hideSetSale();
			
		},
		// 取消铺货
		async cancelPickBtn(item, index){
			uni.showLoading({
				title: "取消铺货中..."
			});			
			const res = await app.request(this.$Url+'/clmsj/goods/getgoods', {
				goods_id: item.goods_id
			});
			this.goodsList[index].select_shop = res.data.data.select_shop;
			uni.hideLoading();
			this.$api.msg('取消铺货成功！');
			
		},
		async btnZhuda(goods_id, index){
			uni.showLoading({
			    title: "设置中..."
			});			
		    const res =await app.request(this.$Url+'/clmsj/goods/shopGoodsZdStatus', {goods_id});
			uni.hideLoading();
			if (res.data.code == 0) {
				this.goodsList[index].shop_goods_zd_status = res.data.goods_zd_status;
			}
			
		},
        detailBtn(goods_id, sid) {
			return;
             uni.navigateTo({
                url: "/pages/product/product?goods_id=" + goods_id + "&sid=" + sid
            });
        },

        async saveBtn(item) {
            const that = this;
            const media_list = item.goodsphoto || [];
            uni.showLoading({
                title: "保存中..."
            });

            let count = 0;
            for (const v of media_list) {
                await this.saveFileToAlbum(v.img_url);
                count++;
                if (count == media_list.length) {
                    uni.hideLoading();
                    const res = await app.request(
                        this.$Url + "/clmsj/goods/savegoodsteam",
                        { goods_id: item.goods_id }
                    );
                    if (res.data.code == 0) {
                        that.$api.msg("保存成功！");
                    }
                }
            }
        },

        saveFileToAlbum(url) {
            return new Promise((resolve, reject) => {
                uni.downloadFile({
                    url: url.replace(/http/, "https"), //仅为示例，并非真实的资源
                    success: res => {
                        if (res.statusCode === 200) {
                            const content_type = res.header[
                                "Content-Type"
                            ].split("/")[0];
                            if (content_type == "image") {
                                uni.saveImageToPhotosAlbum({
                                    filePath: res.tempFilePath,
                                    success: function() {
                                        resolve();
                                    },
                                    fail() {
                                        reject();
                                    }
                                });
                            } else {
                                uni.saveVideoToPhotosAlbum({
                                    filePath: res.tempFilePath,
                                    success: function() {
                                        resolve();
                                    },
                                    fail() {
                                        reject();
                                    }
                                });
                            }
                        }
                    }
                });
            });
        },
        editBtn(id) {
            this.$common.edit_id = id;
            uni.navigateTo({
                url: "/pages/product/add"
            });
        },
        delBtn(id, index) {
            uni.showModal({
                title: "提示",
                content: "确定删除改商品吗？",
                success: res => {
                    if (res.confirm) {
                        uni.request({
                            url: this.$Url + "/clmsj/goods/del_goods",
                            header: {
                                auth: uni.getStorageSync("session_key")
                            },
                            data: {
                                goods_id: id
                            },
                            method: "POST",
                            success: res => {
                                if (res.data.code == 0) {
                                    this.$api.msg("删除成功！");
                                    this.goodsList.splice(index, 1);
                                }
                            }
                        });
                    }
                }
            });
        },
        shareBtn(index) {
            console.log(index);
        },
        videoPlay(e) {
            this.play_media = 1;
            const videoContext = uni.createVideoContext(e.currentTarget.id);
            videoContext.requestFullScreen();
        },
        previewImg(list, index) {
            this.play_media = 1;
            let _list = [];
            list.forEach(v => {
                if (v.status == 0) {
                    _list.push(v.img_url);
                }
            });

            console.log(_list);
            uni.previewImage({
                current: list[index].img_url,
                urls: _list
            });
        },
        changeTabs(index){
			this.selectShopType = index;
			uni.pageScrollTo({
			    duration: 300,
			    scrollTop: 0
			});
			this.loadData("refresh", 1);
			uni.showLoading({
			    title: "正在加载"
			});
		},
        //筛选点击
        tabClick(index) {
            if (this.filterIndex === index && index !== 2) {
                return;
            }
            this.filterIndex = index;
            if (index === 2) {
                this.priceOrder = this.priceOrder === 1 ? 2 : 1;
            } else {
                this.priceOrder = 0;
            }
            uni.pageScrollTo({
                duration: 300,
                scrollTop: 0
            });
            this.loadData("refresh", 1);
            uni.showLoading({
                title: "正在加载"
            });
        },
        //显示分类面板
        toggleCateMask(type) {
            let timer = type === "show" ? 10 : 300;
            let state = type === "show" ? 1 : 0;
            this.cateMaskState = 2;
            setTimeout(() => {
                this.cateMaskState = state;
            }, timer);
        },
        //分类点击
        changeCate(item) {
            this.cateId = item.id;
            this.toggleCateMask();
            uni.pageScrollTo({
                duration: 300,
                scrollTop: 0
            });
            this.loadData("refresh", 1);
            uni.showLoading({
                title: "正在加载"
            });
        },
        //详情
        navToDetailPage(item) {
            //测试数据没有写id，用title代替
            let id = item.store_name;
            uni.navigateTo({
                url: `/pages/product/product?id=${id}`
            });
        },
        stopPrevent() {}
    }
};
